body {
  font: 14px "Century Gothic", Futura, sans-serif;
  margin: 20px;
}

ol,
ul {
  padding-left: 30px;
}

.descending {
  display: flex;
  flex-direction: column-reverse;

  width: 200px;
}
.ascending {
  display: flex;
  flex-direction: column;
  width: 200px;
}

li {
  margin-right: 50px;
}

.board-row:after {
  clear: both;
  content: "";
  display: table;
}

.status {
  margin-bottom: 10px;
}

button {
}

.squareButton button,
.squareButton button:focus,
.squareButton button:focus {
  background-color: transparent;
  border: none;
  height: 34px;
  width: 34px;
  outline: none;
  text-align: center;
  float: left;
}

.square {
  background-color: white;
  border: 1px solid #999;
  float: left;
  font-size: 24px;
  font-weight: bold;
  line-height: 34px;
  height: 34px;
  margin-right: -1px;
  margin-top: -1px;
  padding: 0;
  text-align: center;
  width: 34px;
}

.squareWin {
  background-color: yellow;
  border: 1px solid #999;
  float: left;
  font-size: 24px;
  font-weight: bold;
  line-height: 34px;
  height: 34px;
  margin-right: -1px;
  margin-top: -1px;
  padding: 0;
  text-align: center;
  width: 34px;
}

.square:focus {
  outline: none;
}

.kbd-navigation .square:focus {
  background: #ddd;
}

.game {
  display: flex;
  flex-direction: row;
}

.game-info {
  margin-left: 20px;
}

@media screen and (max-width: 767px) {
  .game {
    display: flex;
    flex-direction: column;
  }

  .game-info {
    margin: 20px 0;
  }
}
